

























































































































.wheel {
    position: relative;
    width: 100%;
    height: 100%;

    .wheel-img {
        width: 100%;
        height: 100%;
    }

    .point-img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 2.026667rem;
        height: 2.533333rem;
    }
}
